<template>
  <div>
    <div class="livingearth-logo">
      <p class="product-title">Ad: Our Product</p>
      <router-link to="/zh-CN/livingearth?x-block=sidebar">
        <img src="https://oixyh3u6e.qnssl.com/livingearth/livingearth.png" width="140px" alt="">
      </router-link>
    </div>
     
    <div class="vux-box">
      <svg style="width:60px;height:60px" version="1.1" id="" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="1024px" height="1024px" viewBox="0 0 1024 1024" enable-background="new 0 0 1024 1024" xml:space="preserve"><path class="svgpath" data-index="path_0" fill="#35495e" d="M512 816.64 51.2 985.6l460.8-947.2 460.8 947.2L512 816.64 512 816.64zM509.44 207.36 189.44 862.72l317.44-117.76L506.88 207.36 509.44 207.36zM509.44 207.36"></path></svg>
      <br>
      <span class="vux-version">2.x</span>

      <h1 class="vux-title">VUX</h1>
      
      <div class="vux-center vux-sub-title">Proudly Powered by 
      <br>
      WeUI + Vue + Webpack</div>

      <div>
        <br>
        <a target="_blank" class="vux-github" href="https://github.com/airyland/vux">
        <svg id="i-github" viewBox="0 0 64 64" width="32" height="32">
  <path stroke-width="0" fill="currentColor" d="M32 0 C14 0 0 14 0 32 0 53 19 62 22 62 24 62 24 61 24 60 L24 55 C17 57 14 53 13 50 13 50 13 49 11 47 10 46 6 44 10 44 13 44 15 48 15 48 18 52 22 51 24 50 24 48 26 46 26 46 18 45 12 42 12 31 12 27 13 24 15 22 15 22 13 18 15 13 15 13 20 13 24 17 27 15 37 15 40 17 44 13 49 13 49 13 51 20 49 22 49 22 51 24 52 27 52 31 52 42 45 45 38 46 39 47 40 49 40 52 L40 60 C40 61 40 62 42 62 45 62 64 53 64 32 64 14 50 0 32 0 Z"></path>
      </svg>
</a>
      </div>
    </div>

    <div style="margin-left:220px;">
      <ul class="sidebar-headings">
        <li
          class="sidebar-heading"
          :class="{'has-children': hasChildren(heading.index), visible: isVisible(heading.level, heading.parent)}"
          v-for="heading in headings"
          :data-level="heading.level">
          <router-link
            exact
            @click.native="navigate(heading.slug)"
            class="sidebar-heading-anchor"
            :class="{active: activeId === heading.slug}"
            :to="{query: {id: heading.slug}}"
            v-html="heading.text">
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import {$, isMobile} from 'utils/dom'

  export default {
    props: {
      headings: {
        type: Array,
        required: true
      }
    },
    computed: {
      ...mapState(['activeId', 'config']),
      visibleBlockIndexes() {
        if (!this.activeId) return []
        const indexes = []

        const active = this.headings.filter(heading => {
          return this.activeId === heading.slug
        })[0]
        if (!active) return []
        indexes.push(active.index)

        const getParent = heading => {
          indexes.push(heading.parent)
          const parent = this.headings.filter(h => {
            return h.index === heading.parent
          })[0]
          if (parent) getParent(parent)
        }

        const parent = this.headings[active.index]
        if (parent) getParent(parent)

        return indexes.filter(i => i >= 0)
      }
    },
    methods: {
      ...mapActions(['jumpToId']),
      hasChildren(index) {
        return this.headings.filter(heading => {
          return heading.parent === index
        }).length > 0
      },
      isVisible(level, index) {
        if (level <= (this.config.tocVisibleDepth || 4)) return true
        return this.visibleBlockIndexes.indexOf(index) !== -1
      },
      navigate(slug) {
        this.jumpToId(slug)
      }
    }
  }
</script>

<style>
/**
https://fonts.proxy.ustclug.org/css?family=Josefin+Sans:600
*/

/* latin-ext */
@font-face {
  font-family: 'Josefin Sans';
  font-style: normal;
  font-weight: 300;
  src: local('Josefin Sans Light'), local('JosefinSans-Light'), url(https://fonts-gstatic.proxy.ustclug.org/s/josefinsans/v9/C6HYlRF50SGJq1XyXj04z7sKtFnhOiVZh9MDlvO1Vys.woff2) format('woff2');
  unicode-range: U+0100-024F, U+1E00-1EFF, U+20A0-20AB, U+20AD-20CF, U+2C60-2C7F, U+A720-A7FF;
}
/* latin */
@font-face {
  font-family: 'Josefin Sans';
  font-style: normal;
  font-weight: 300;
  src: local('Josefin Sans Light'), local('JosefinSans-Light'), url(https://fonts-gstatic.proxy.ustclug.org/s/josefinsans/v9/C6HYlRF50SGJq1XyXj04z0ZRWJQ0UjzR2Uv6RollX_g.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}

.big-title {
  font-size: 3em!important;
  font-family: 'Josefin Sans', sans-serif;
  color: #42b983;
}

.product-title {
  border-top: 1px solid #333;
  padding-top: 15px;
  font-size: 14px;
  color: #666;
  text-align: center;
  font-family: 'Josefin Sans', sans-serif;
}

.livingearth-logo {
  position: fixed;
  bottom: 15px;
  left: 40px;
  z-index: 999;
}

.vux-version {
  text-align: center;
  width: 50px;
  color: #ccc;
  text-shadow: 0 0 6px rgba(0,120,255,0.9);
}

.vux-title {
  font-weight: normal;
  font-family: 'Josefin Sans', sans-serif;
  text-align: center;
  font-size: 40px;
  margin: 20px 0 20px 0;
}

.vux-github {
  color: #fff;
}

.vux-sub-title {
  font-size: 14px;
  color: #ececec;
  font-family: 'Josefin Sans', sans-serif;
}
  .vux-center {
    text-align: center;
  }
  .vux-box {
    color: #fff;
    padding: 15px;
    width: 220px;
    float:left;
    background-color:#171717;
    height:100%;
    position:fixed;
    text-align: center;
    left:0;
    top:0;
  }
  .sidebar-headings {
    list-style: none;
    margin: 0;
    margin-top: 10px;
    padding-left: 0;
    .sidebar-heading {
      line-height: 1.4;
      &:not(.visible) {
        display: none;
      }
      &[data-level="2"] {
        font-weight: bold;
        .sidebar-heading-anchor {
          color: #333;
        }
      }
      &[data-level="4"] {
        font-size: 13px;
        .sidebar-heading-anchor {
          padding-left: 35px;
        }
      }
      &[data-level="5"] {
        font-size: 13px;
        .sidebar-heading-anchor {
          padding-left: 50px;
        }
      }
      &[data-level="6"] {
        font-size: 12px;
        .sidebar-heading-anchor {
          padding-left: 66px;
        }
      }

      .sidebar-heading-anchor {
        color: #666;
        padding: 7px 20px;
        padding-left: 17px;
        display: block;
        &.active, &:hover {
          color: #42b983;
        }
      }
    }
  }
  @media screen and (max-width: 768px) {
    .sidebar-headings {
      border-top: 1px solid #e2e2e2;
      padding-top: 10px;
    }
    .sidebar-heading-anchor {
      padding: 7px 10px;
    }
  }
</style>



